body {
  margin: 0px;
  font-family: 'Inter', sans-serif;
  font-weight: 400;
  background-color: #FF9800;
}

h1 {
  font-family: 'Inter', sans-serif;
  font-weight: 700;
}

a {
  text-decoration: none;
  color: #fff;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.card {
  background-color: #23252C;
  width: 317px;
  border-radius: 10px;
  margin-top: 20px;
  margin-left: 38%;
  box-shadow: 0px 0px 40px 5px #fff;
}

.Profile--Image {
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
}

.Content--Section {
  width: 317px;
  height: 350px;
  background-color: #23252C;
  margin-top: 0px;
}

.Content--Section h1 {
  color: #fff;
  text-align: center;
  font-size: 30px;
  margin-bottom: 0px;
}

.Content--Section h5 {
  color: #F3BF99;
  text-align: center;
  font-size: 12.8px;
  margin-top: 0px;
  margin-bottom: 0px;
}

.Content--Section p {
  color: #fff;
  text-align: center;
  font-size: 10.24px;
  margin-top: 5px;
}

.Content--Buttons {
  display: flex;
  margin-left: 8%;
  width: 100%;
}

.Content--Button {
  width: 115px;
  height: 34px;
  background-color: #fff;
  border-radius: 8px;
  margin: 10px;
}

.Content--Email {
  position: relative;
  top: 8px;
  left: 30px;
}

.Content--Email--p {
  color: #374151;
  font-size: 14px;
  position: relative;
  top: -15.5px;
  left: 15px;
}

.Content--linkedin {
  position: relative;
  top: 10px;
  left: 15px;
}

.Content--linkedin--p {
  color: #ffffff;
  font-size: 14px;
  position: relative;
  top: -16px;
  left: 10px;
}

.Content--Text {
  width: 100%;
  margin-left: 10%;
}

.Content--Text h3 {
  font-weight: 700;
  font-size: 16px;
  color: #fefefe;
  margin-bottom: 0px;
}

.Content--Text p {
  width: 80%;
  text-align: left;
  font-weight: 400;
  color: #DCDCDC;
}

.Footer--Section {
  background-color: #161619;
  height: 64px;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
  margin-bottom: 20px;
}

.Footer--Section img {
  margin-top: 20px;
  padding-right: 20px;
}

